/* ************************************************************* */
.hero-container {
    /* background: url('../images/fromthesky.jpeg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    color: blueviolet;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.hero-container > h1 {
    /* color: blueviolet; */
    font-size: 80px;
    margin-top: -100px;
    font-weight: 700;
    /* background: #0e8dbc; */
